<template>
    <div class="wrap">
        <div v-if="!api.loading">
            <div class="tit"> 
                <div class="bx sun"><b></b></div>
                <div class="bx"></div>
                <div class="bx"></div>
                <div class="bx"></div>
                <div class="bx"></div>
                <div class="bx"></div>
                <div class="bx sat"><b></b></div>
            </div>
            <div class="cont">
                <!-- // y  c  sun  sat  n -->
                <div class="bx" v-for="item in list" :key="item.key" v-bind:class="item.isHoliday == 'sun'?'sun':(item.isHoliday == 'sat'?'sat':(item.isHoliday =='y'?'rest1':(item.isHoliday =='c'?'rest2':'')))">
                    <b v-if="item.date !== 0">{{item.date}}</b>
                    <span>{{item.rest}}</span>
                </div>
            </div>
            <p class="info" v-if="list[0].year > 2020 || list[0].year < 2015">>*  Api 2015    .</p>
            <p class="info" v-if="api.error">*  Api  !</p>
        </div>
        <div v-else class="bx_loading">
            <h3>Loading...</h3>
        </div>
    </div>
</template>
<script>
export default {
    props:{
        'list':Array,
        'month':Number,
        'api':{}
    },
    created(){
    }
}
</script>